/* Footer.css */

.footer {
    background-color: #000;
    color: #fff;
    padding: 1rem 0;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .contact-links a {
    text-decoration: none;
    color: #fff;
    font-size: 24px;
    margin: 0 10px;
  }
  
  